
import Vue from "vue"

export default Vue.extend({
  data() {
    return {
      viewed: true,
    }
  },
  mounted() {
    this.viewed = Boolean(localStorage.getItem("sponsor-popup-viewed")) || false
  },
  methods: {
    dismissMessage() {
      localStorage.setItem("sponsor-popup-viewed", "true")
      this.viewed = true
    },
  },
})
